import { OmniBasePage } from './../omni-base/omni-base.page';
import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOptionsPsLabel, IOptionsPsTemplateView } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { IOmniMandatesApproval, IOptionsPsMandatesListExposed } from 'src/app/commonBussinessSRC/psComponents/ps-mandates-list/ps-mandates-list.component.interface';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { IMandatesResponse } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { PsDateFormatPipe } from 'src/app/commonSRC/psPipes/ps-date-format/ps-date-format.pipe';
import { PsCurrencyPipe } from 'src/app/commonSRC/psPipes/ps-currency/ps-currency.pipe';

@Component({
  selector: 'app-mandates-approval',
  templateUrl: './mandates-approval.page.html',
})
export class MandatesApprovalPage extends OmniBasePage implements OnInit {

  // requestWasSent = true;
  mandatesListOptions: IOptionsPsMandatesListExposed;
  private formGroup = new FormGroup({});
  public templateViewOptions: IOptionsPsTemplateView = {
    group: this.formGroup
  };
  public noMandatesFound = false;
  private psDatePipe = new PsDateFormatPipe();
  private psCurrency = new PsCurrencyPipe();
  public noMandatesFoundOptions: IOptionsPsLabel = {
    // create a key for mandates
    labelKey: 'no_general_records_found_key',
    previewMode: false
  };

  constructor(public logger: LoggerService, private omniPull?: OmniPullService) {
    super()
  }

  ngOnInit() {
    super.init();
    // this.baseFormGroup = this.formGroup;
    this.mandatesListOptions = {
      isEditable: true,
    };
    this.loadMandatesList();
  }


  loadMandatesList() {
    const generalRequestData: any = {
      commonParametersList: {
        status: "A"
      }
    };
    this.returnMandates(generalRequestData);
  }

  private async returnMandates(requestData: any) {
    // this.requestWasSent = true;
    // const result = await this.omniPull.returnMandatesList(requestData).catch(error => {
    //   this.logger.error('Error: While fetching mandates approvals  :', error);
    // });
    const result = {
      gridModel: [
        {
          date: new Date,
          amount: "111",
          frequency: "Monthly, 2 payments",
          payIdType: "1234",
          dateRange: new Date,
          benefName: "Beneficiary Name 1",
          benefPhone: "01 23456"
          // valueDate?: string,
        },
        { 
          date: new Date,
          amount: "100",
          frequency: "Monthly, 21 payments",
          payIdType: "112233",
          dateRange: new Date,
          benefName: "Beneficiary Name 2",
          benefPhone: "01 24689"
           // valueDate?: string,
        }
      ]
    }
    if (result && result.gridModel.length > 0) {
      this.mandatesListOptions.listOfOptions = this.populateMandatesApprovals(result.gridModel);
    } else {
      this.noMandatesFound = true;
      this.mandatesListOptions.listOfOptions = [];
    }
    // this.requestWasSent = false;
  }

  private populateMandatesApprovals(mandateslist: IMandatesResponse[]): IOmniMandatesApproval[] {
    const mandatesApproval: IOmniMandatesApproval[] = [];
    for (const iterator of mandateslist) {
      const mandates: IOmniMandatesApproval = {
        date: this.psDatePipe.transform(iterator.valueDate, 'DD/MM/YYYY').toString(),
        payIdType: iterator.payIdType,
        // change the frequency formula
        currencyBriefNameEnglish: iterator.currencyBriefNameEnglish,
        frequency: iterator.frequency,
        benefName: iterator.benefName,
        currency: iterator.currency,
        mandatesAmount:  this.psCurrency.transform((iterator.amount || 0).toString(), 2) 
         + ' (' + iterator.currencyBriefNameEnglish + ')',
        dateRange: this.psDatePipe.transform(iterator.valueDate, 'DD/MM/YYYY').toString() + ' - ' + this.psDatePipe.transform(iterator.valueDate, 'DD/MM/YYYY').toString(),
        benefPhone: iterator.benefPhone
      }
      mandatesApproval.push(mandates);
    }
    return mandatesApproval;
  }

}
